.outer {
  border: 1px solid black;
  background-color: white;
  width: var(--base-search-width);
  transition: 2s ease;
}

.listItem {
  list-style-type: none;
}

.listItem:hover {
  cursor: pointer;
  background-color: #ddd;
}

.listItemLink {
  color: black;
  text-decoration: none;
}
